"use client";

import { useState } from "react";
import { motion } from "framer-motion";
import { useRouter } from "next/navigation";
import LeftSideLayout from "@/components/common/"; // ✅ Reusable left layout

export default function GetStartedPage() {
  const router = useRouter();
  const [phone, setPhone] = useState("");
  const [agreed1, setAgreed1] = useState(true);
  const [agreed2, setAgreed2] = useState(true);

  return (
    <div className="h-screen w-screen overflow-hidden flex flex-col bg-[var(--color-bg-neutral-50)]">
      {/* ✅ NAVBAR */}
      <header className="w-full bg-[var(--color-bg-neutral-50)] border-b border-[var(--color-btn-teritary-200)] z-50 py-4 px-28">
        <div className="flex items-center space-x-4">
          <img src="/logo.png" alt="Workly Logo" className="h-6 w-auto" />
        </div>
      </header>

      {/* ✅ MAIN SECTION */}
      <main className="flex flex-1 h-full m-0 p-0">
        {/* ✅ LEFT SIDE — Reused from common layout */}
        <LeftSideLayout />

        {/* ✅ RIGHT SIDE */}
        <div className="w-1/2 h-full flex items-center justify-center bg-[var(--color-btn-teritary-100)]">
          <motion.div
            key="form"
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5 }}
            className="w-full max-w-lg py-8 px-12 rounded-2xl shadow-sm flex flex-col justify-center bg-[var(--color-bg-neutral-50)]"
          >
            <h2 className="text-2xl font-semibold text-[var(--color-text-primary)] mb-3 text-left">
              Let’s get started
            </h2>

            <p className="text-xs text-[var(--color-text-secondary)] mb-8 text-left">
              Enter your phone number to continue. We’ll send you a verification code.
            </p>

            <form
              className="space-y-6"
              onSubmit={(e) => {
                e.preventDefault();
                router.push("/getstarted/verification");
              }}
            >
              <div>
                <label
                  htmlFor="phone"
                  className="block text-sm font-medium text-[var(--color-text-primary)] mb-2"
                >
                  Phone number
                </label>

                <div className="flex items-center border border-[var(--color-btn-teritary-200)] rounded-lg bg-[var(--color-bg-neutral-50)] focus-within:ring-2 focus-within:ring-[var(--color-primary)]">
                  <span className="flex items-center pl-4 pr-3 text-[var(--color-text-secondary)] text-base font-medium">
                    +1
                  </span>
                  <div className="h-6 w-px bg-[var(--color-btn-teritary-200)]"></div>
                  <input
                    type="tel"
                    id="phone"
                    placeholder="000 000 0000"
                    className="w-full px-3 py-2 text-[var(--color-text-primary)] placeholder-[var(--color-text-muted)] bg-transparent focus:outline-none"
                    value={phone}
                    onChange={(e) => setPhone(e.target.value)}
                  />
                </div>
              </div>

              {/* ✅ Checkboxes */}
              <div className="space-y-3">
                <label className="flex items-start space-x-2 text-xs text-[var(--color-text-secondary)]">
                  <input
                    type="checkbox"
                    checked={agreed1}
                    onChange={() => setAgreed1(!agreed1)}
                    className="mt-0.5 accent-[var(--color-primary)]"
                  />
                  <span>
                    I agree to receive Workly updates and notifications via SMS or WhatsApp.
                  </span>
                </label>

                <label className="flex items-start space-x-2 text-xs text-[var(--color-text-secondary)]">
                  <input
                    type="checkbox"
                    checked={agreed2}
                    onChange={() => setAgreed2(!agreed2)}
                    className="mt-0.5 accent-[var(--color-primary)]"
                  />
                  <span>
                    I accept the{" "}
                    <a
                      href="#"
                      className="text-[var(--color-primary)] underline"
                    >
                      Terms and Conditions
                    </a>
                    , and Privacy Policy.
                  </span>
                </label>
              </div>

              {/* ✅ Submit Button */}
              <button
                type="submit"
                className="w-full bg-[var(--color-btn-primary-500)] hover:bg-[var(--color-btn-primary-600)] text-[var(--color-secondary)] py-3 rounded-lg font-semibold transition hover:cursor-pointer"
              >
                Get Started
              </button>
            </form>
          </motion.div>
        </div>
      </main>
    </div>
  );
}
